.empreendimentos-container {
  display: flex;
  background-color: #f5f5f5;
  min-height: 100vh;
}

.empreendimentos-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 16px;
}

.empreendimentos-main {
  display: flex;
  margin-top: 16px;
}

.empreendimentos-listagem {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 16px;
  padding-left: 16px;
}
